import { Container, Box, Typography, Grid } from "@material-ui/core";
import React from "react";
import { alpha } from "@material-ui/core/styles";
import donateFill from "@iconify/icons-bx/bxs-donate-heart";
import peopleFill from "@iconify/icons-eva/people-outline";

import palette from "../../../../../theme/palette";
import { DonorsDividerProps } from "./components/DonorsDivider";
import {
  DonorsGrowth,
  DonorsDivider,
  TransactionCountry,
  RecentActivities,
} from "./components";
import useAxios from "../../../../../utils/useAxios";
import { useQuery, UseQueryResult } from "react-query";
import { DonorsApiProps } from "../../../../../utils/typeHelpers";
import Loader from "../../../../../components/Loader";
import ApiErrorPage from "../../../../Errror/ApiErrorPage";

const Donors = () => {
  const axiosInstance = useAxios();
  const donorsQuery: UseQueryResult<DonorsApiProps, Error> = useQuery(
    ["donorsDashboard"],
    async () => {
      const response = await axiosInstance.get(`/dashboard/donors/`);

      return response.data;
    }
  );
  const { isLoading, isError, data, error } = donorsQuery;

  return (
    <div>
      {isLoading ? (
        <Loader />
      ) : isError && data === undefined ? (
        <ApiErrorPage error={error ? error : new Error("Network Error")} />
      ) : (
        <Container maxWidth="xl">
          <Box sx={{ pb: 5 }}>
            <Typography
              sx={{
                fontSize: 32,
                fontWeight: "bold",
                height: 40,
                color: "#212529",
              }}
            >
              Donors
            </Typography>
          </Box>
          <Grid container spacing={3} sx={{ pb: 5 }}>
            <Grid item xs={12} sm={6} md={3}>
              <DonorsDivider
                icon={demoData[0].icon}
                value={data?.donors ? data?.donors : 0}
                label={demoData[0].label}
                subvalue={data?.new_donors ? data?.new_donors : 0}
                sublabel={demoData[0].sublabel}
                bgColor={demoData[0].bgColor}
                color={demoData[0].color}
                backgroundImage={demoData[0].backgroundImage}
              />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <DonorsDivider
                icon={demoData[1].icon}
                value={data?.donations ? data?.donations : 0}
                label={demoData[1].label}
                subvalue={data?.donations_amount ? data?.donations_amount : 0}
                sublabel={demoData[1].sublabel}
                bgColor={demoData[1].bgColor}
                color={demoData[1].color}
                backgroundImage={demoData[1].backgroundImage}
              />
            </Grid>
            <Grid item xs={12} sm={12} md={6}>
              <DonorsGrowth
                label="Donor Retention rate"
                value={31}
                sublabel="Donor Growth rate"
                subvalue={12}
              />
            </Grid>
            <Grid item xs={12} sm={12} md={5}>
              <TransactionCountry
                chartData={
                  data?.donors_by_country ? data?.donors_by_country : []
                }
              />
            </Grid>
            {data?.recent_activity !== null &&
              data?.recent_activity !== undefined && (
                <Grid item xs={12} sm={12} md={7}>
                  <RecentActivities recentActivity={data?.recent_activity} />
                </Grid>
              )}
          </Grid>
        </Container>
      )}
    </div>
  );
};

export default Donors;
const demoData: DonorsDividerProps[] = [
  {
    label: "Donors",
    value: 180,

    sublabel: "New Donors",
    subvalue: 10,
    bgColor: palette.primary.lighter,
    color: palette.primary.darker,
    backgroundImage: `linear-gradient(135deg, ${alpha(
      palette.primary.dark,
      0
    )} 0%, ${alpha(palette.primary.dark, 0.24)} 100%)`,
    icon: peopleFill,
  },
  {
    label: "Donations",
    value: 190,
    sublabel: "Amount",
    subvalue: 3500,
    bgColor: palette.success.lighter,
    color: palette.success.darker,
    backgroundImage: `linear-gradient(135deg, ${alpha(
      palette.success.dark,
      0
    )} 0%, ${alpha(palette.success.dark, 0.24)} 100%)`,
    icon: donateFill,
  },
];
